import no_photo from '../../assets/no_photo.png'
import cssUsers from './Users.module.css';
import React from 'react';
import { NavLink } from 'react-router-dom';

export const User = ({user,followInProgress,unfollow,follow,...props}) =>  {
  

return (
    <div  className={cssUsers.user_block}>
        <div className={cssUsers.avatar}>
            <img src={(user.photos.small != null ? user.photos.small : no_photo)}/>
        </div>
        <div className={cssUsers.user_left_block}>
            <div className={cssUsers.user_name}>
            <NavLink to={'/profile/' + user.id} className={`${cssUsers.link}`} activeClassName={cssUsers.active}>{user.name}</NavLink>
                
            </div> 
            <div className={cssUsers.user_status}>
                    {user.status_text}
            </div>
            <div className={cssUsers.user_position}>
                    {user.location.country + ", " + user.location.city}
            </div>
        </div>
        {user.followed ? 
            <button disabled={followInProgress.some(id => id === user.id)} className={cssUsers.unfollowButton} onClick={ ()=>{unfollow(user.id);} } >Отписаться</button>
            :
            <button  disabled={followInProgress.some(id => id === user.id)} className={cssUsers.followButton} onClick={ ()=>{follow(user.id)} }>Подписаться</button>
        }
    </div>

)
        

}

export default User;