import css from './Paginator.module.css';
import React, {useState} from 'react';

let Paginator = ({totalItemsCount, pageSize, currentPage,onPageChanged, portionSize = 5}) => {
    
    let pagesCount = Math.ceil(totalItemsCount /  pageSize); 
    let pages = [];
    for (let i=1; i <= pagesCount; i++) {
        pages.push(i)
    }

    let portionCount = Math.ceil(pagesCount /  portionSize); 
    let [portionNumber,setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * ( portionSize ) + 1;
    let rigthPortionPageNumber = portionNumber * portionSize;
    return (

            <div className={css.pagination_wrap}>
                { ((portionNumber > 1) ? <button onClick={()=>{setPortionNumber(portionNumber-1)}}>prev</button>: "")}
                {pages
                .filter(el=> el>= leftPortionPageNumber && el <= rigthPortionPageNumber)
                .map( 
                    el => {
                        return (<span key={el} className={currentPage === el  ? css.selectedPage : ''} onClick={(e)=>{onPageChanged(el)}}>{el}</span>)
                    }
                )}
                { ((portionCount > portionNumber) ? <button onClick={()=>{setPortionNumber(portionNumber+1)}}>next</button>: "")}
            </div>
        )
}


export default Paginator;