import no_photo from '../../assets/no_photo.png'
import cssUsers from './Users.module.css';
import React from 'react';
import { NavLink } from 'react-router-dom';
import Paginator from '../common/Paginator/Paginator';
import User from './User';


let Users = ({
                //Props for paginator
                currentPage,onPageChanged,totalUsersCount, pageSize, 
                //Props for userList
                users,follow,unfollow,pages,followInProgress,toggleFollowingInProgress
            }) => {
    return (
            <div>
                <h2>Пользватели</h2>
                <Paginator currentPage={currentPage} onPageChanged={onPageChanged} totalItemsCount={totalUsersCount} pageSize={pageSize}/>
                <UsersList 
                    users={users} 
                    follow={follow} 
                    unfollow={unfollow} 
                    pages={pages} 
                    toggleFollowingInProgress={(val,userId)=>{toggleFollowingInProgress(val,userId)}}
                    followInProgress={followInProgress}
                />
            </div>
        )
}


const UsersList = (props) => {
    const follow = (userId) => {
        console.log(props);  
        props.follow(userId);     
        
    }
    
    const unfollow = (userId) => {
        console.log(props);
        props.unfollow(userId);       
    } 
    return (<div >
            {
                props.users.map( user => {
                    return (
                        <User 
                            key={user.id} 
                            user={user} 
                            followInProgress={props.followInProgress} 
                            follow={follow} 
                            unfollow={unfollow}
                        />
                    )
                })
            }
        </div>
    )
}

export default Users;