import { connect } from "react-redux";
import { follow, unfollow,getUsers } from "../../redux/users-reducer";
import Users from './Users.jsx';
import Preloader from '../common/Preloader/Preloader.js';
import React from 'react';
import { withAuthRedirect } from "../../hoc/withAuthRedirect";
import { getPageSize, getUsersListSuper, getTotalUsersCount, getCurrentPage, getStatusFetching, getfollowingInProgress } from "../../redux/users-selectors";


class UsersContainer extends React.Component {
    componentDidMount() {
        const {currentPage,pageSize} = this.props; 
        this.props.getUsers(currentPage,pageSize);
    }
    onPageChanged = (newPage) => {
        const {pageSize} = this.props;
        this.props.getUsers(newPage,pageSize);

    }

    
    render() {
            return ( 
            <>
            { this.props.isFetching ? <Preloader/>: null}
            <Users 
                totalUsersCount={this.props.totalUsersCount} 
                pageSize={this.props.pageSize}
                currentPage={this.props.currentPage}
                onPageChanged={this.onPageChanged}
                users={this.props.users}
                follow={this.props.follow}
                unfollow={this.props.unfollow}
                toggleFollowingInProgress={(val,userId)=>{this.props.toggleFollowingInProgress(val,userId)}}
                followInProgress={this.props.followInProgress}
            />
            </>
            )
            
    }
}

let mapStateToProps = (state) => {
    return {
        users: getUsersListSuper(state),
        pageSize: getPageSize(state),
        totalUsersCount:  getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getStatusFetching(state),
        followInProgress: getfollowingInProgress(state)
    }
}


export default withAuthRedirect(connect(mapStateToProps,{
        follow, unfollow,  getUsers
    }

)(UsersContainer))